<template>
  <div class="recipes-container">
    <el-tabs v-model="activeName">
      <el-tab-pane label="" name="detail">
        <div class="success-container">
          <div class="success-title" style="margin-bottom: 30px" />
          <div class="success-item">
            <div class="label">
              <div class="avatar">
                <el-avatar
                  :size="'small'"
                  :src="taskDetail.user && taskDetail.user.avatar"
                />
              </div>
              :
            </div>
            <div class="content">
              {{ userName }}&nbsp;&nbsp;
              <svg-icon v-if="taskDetail.is_replace" icon-class="replace" />
            </div>
          </div>
          <div class="success-item baby-item">
            <div class="label">:</div>
            <div class="content">
              {{ taskDetail.family_member && taskDetail.family_member.name }}
              &nbsp;&nbsp;&nbsp;
              <el-tooltip effect="dark" :content="likeTooltip" placement="top">
                <span :class="['like', taskDetail.like ? '' : 'unlike']">
                  <i class="iconfont icon-dianzan" />
                </span>
              </el-tooltip>
            </div>
          </div>
          <div class="success-item">
            <div class="label">:</div>
            <div class="content">
              {{
                taskDetail.recipe_template && taskDetail.recipe_template.title
              }}
            </div>
          </div>
          <div class="success-item">
            <div class="label">:</div>
            <div class="content">
              {{ taskDetail.title }}
            </div>
          </div>
          <div class="success-item">
            <div class="label">:</div>
            <div class="content">
              {{ taskDetail.desc ? taskDetail.desc : '' }}
            </div>
          </div>
          <div class="success-item">
            <div class="label">:</div>
            <div class="content">
              {{ createTime }}
            </div>
          </div>
          <div class="success-item">
            <div class="label">:</div>
            <div class="content">
              {{ executeDate }}
            </div>
          </div>
          <div class="success-item">
            <div class="label">:</div>
            <div class="content">
              <c-hot-table
                v-model="taskDetail.content"
                :height="300"
                :readonly="true"
              />
            </div>
          </div>
          <div class="success-item">
            <div class="label">:</div>
            <div class="content" style="line-height: 25px">
              {{ taskDetail.description }}
            </div>
          </div>
          <div class="time-stamp">
            {{ new Date(taskDetail.sendTime) | formatTime() }}
          </div>
          <div v-if="taskDetail.task_leave_message">
            <div class="success-item">
              <div class="label">:</div>
              <div class="content">{{ taskDetail.task_leave_message.ask }}</div>
            </div>
            <div class="time-stamp">
              {{
                new Date(taskDetail.task_leave_message.askTime) | formatTime()
              }}
            </div>
            <div v-if="taskDetail.task_leave_message.answerContent">
              <div class="success-item">
                <div class="label" style="line-height: 25px">
                  <div>
                    :
                  </div>
                  <div>
                    ({{
                      taskDetail.replyer && taskDetail.replyer.name
                    }})&nbsp;&nbsp;
                  </div>
                </div>
                <div class="content" style="line-height: 25px">
                  {{ taskDetail.task_leave_message.answerContent }}
                </div>
              </div>
              <div class="time-stamp reply-time">
                {{
                  new Date(taskDetail.task_leave_message.answerTime)
                    | formatTime()
                }}
              </div>
            </div>
            <div
              v-if="!taskDetail.task_leave_message.answerContent"
              class="comment"
            >
              <div class="comment-item">
                <div class="comment-label" @click="addComment = true"></div>
                <div v-if="addComment">
                  <div style="margin-bottom: 10px"></div>
                  <div class="comment-content">
                    <el-input
                      v-model="answerContent"
                      placeholder=""
                      type="textarea"
                      maxlength="1000"
                      show-word-limit
                      rows="5"
                    />
                  </div>
                  <div style="margin-top: 15px;text-align: center">
                    <el-button @click="addComment = false"></el-button>
                    <el-button type="primary" @click="send"></el-button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="preview">
        <div class="recipes-image">
          <HtmlToImage ref="htmlToImage" class="recipe-preview">
            <div
              class="recipe"
              :style="{
                paddingTop: '20px',
                backgroundImage: `url(${
                  curRecipeBgOpt() ? curRecipeBgOpt().recipeBg : ''
                })`
              }"
            >
              <img
                class="recipe__header-img"
                width="100%"
                :src="curRecipeBgOpt() ? curRecipeBgOpt().recipeHeadBg : ''"
                alt=""
              />
              <div class="recipe__body">
                <div class="recipe__body__header">
                  <span class="recipe__body__header__title">
                    {{ taskDetail.title }}
                  </span>
                  <span class="recipe__body__header__sign">
                    {{
                      taskDetail.sender && taskDetail.sender.name
                    }}</span
                  >
                </div>
                <c-table :data="effHtData" />
                <div class="recipe__body__notes">
                  <div v-html="taskDetail.description" />
                </div>
              </div>
            </div>
          </HtmlToImage>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { checkDesc, replayUser } from '@/api/recipes'
import { localRead } from '@/utils'
import { getEff2dArrayByCols } from '@/libs/arrayUtils'
import { recipeBgOpts } from '@/utils/recipeBgOpts.js'
import CHotTable from '@/components/ChotTable/CHotTable'
import CTable from '@/components/Ctable/CTable'
import HtmlToImage from '@/components/html2image/HtmlToImage'
import _ from 'lodash'

export default {
  name: 'TaskDetail',
  components: {
    CHotTable,
    CTable,
    HtmlToImage
  },
  data() {
    return {
      addComment: false,
      answerContent: '',
      curRecBgOpts: recipeBgOpts,
      effHtData: [],
      activeName: 'detail',
      currentTask: {},
      taskDetail: {
        user: '',
        title: '',
        content: []
      }
    }
  },
  watch: {
    taskDetail: {
      handler: function(val) {
        this.effHtData = getEff2dArrayByCols(val.content)
      },
      deep: true
    },
    activeName(val) {
      if (val === 'preview') {
        this.getTaskDetail()
      }
    }
  },
  mounted() {
    this.getTaskDetail()
  },
  computed: {
    userName() {
      return _.get(this.taskDetail, 'user.remark_name')
        ? _.get(this.taskDetail, 'user.remark_name')
        : _.get(this.taskDetail, 'user.nickname')
    },
    createTime() {
      return this.formatTime(new Date(this.taskDetail.createTime))
    },
    executeDate() {
      return this.formatTime(
        new Date(this.taskDetail.executeTime),
        '{y}-{m}-{d}'
      )
    },
    likeTooltip() {
      return this.taskDetail.like ? '' : ''
    }
  },
  methods: {
    getTaskDetail() {
      this.currentTask = JSON.parse(localRead('currentTask'))
      checkDesc(this.currentTask._id).then(res => {
        if (res.code === 20000) {
          this.taskDetail = res.data
        }
      })
    },
    send() {
      // 
      if (!this.answerContent) {
        this.$message.warning('')
      }
      replayUser(this.currentTask._id, {
        answerContent: this.answerContent
      }).then(res => {
        if (res.code === 20000) {
          this.$message.success('')
          this.$router.push({
            path: 'detail',
            query: {
              id:
                this.currentTask.family_member &&
                this.currentTask.family_member._id
            }
          })
        }
      })
    },
    curRecipeBgOpt() {
      if (this.taskDetail.recipe_template) {
        const recipeBgFilterOpts = this.curRecBgOpts.filter(
          opt => opt.type === this.taskDetail.recipe_template.bg_type
        )
        return recipeBgFilterOpts.length === 1 ? recipeBgFilterOpts[0] : {}
      }
    }
  }
}
</script>
<style lang="scss">
.recipes-container {
  .el-tabs__content {
    overflow: visible;
  }
}
.comment-content {
  .el-textarea {
    .el-input__count {
      bottom: 5px;
      background-color: transparent;
    }
  }
}
</style>
<style scoped lang="scss">
@import '@/styles/healthManagement/addtask.scss';
@import '@/styles/recipes/recipes.scss';
.label {
  font-weight: bold;
}
.recipes-container {
  margin-left: 20px;
  width: 100%;
  .recipe__body__notes {
    white-space: pre-line;
  }
  .recipes-image {
    margin-left: 200px;
    padding-top: 30px;
    width: 640px;
  }
}
.comment {
  padding-left: 55px;
  color: #17233d;
  .comment-item {
    padding: 0 55px;
    font-size: 16px;
    margin-bottom: 30px;
    .comment-label {
      margin-top: 10px;
      line-height: 50px;
      color: #0085ff;
      cursor: pointer;
    }
    .comment-content {
      padding-right: 100px;
    }
  }
}
.success-container {
  margin-left: -100px;
}
.baby-item {
  position: absolute;
  top: 0;
  right: 350px;
  width: 500px;
}
.like {
  color: #ffb718;
}
.unlike {
  color: #999;
}
.time-stamp {
  margin-top: -50px;
  padding-left: 193px;
  line-height: 50px;
  color: #999;
}
.reply-time {
  margin-top: -80px;
}
</style>
